.btn {
  padding: 5px 30px;
  background: $btn-bg-color;
  border-radius: 17px;
  font-size: 24px;
  line-height: 40px;

  color: $white;
  transition: $transition;
  text-decoration: none;
  &:hover,
  &:focus {
    background-color: $btn-bg-color-hover;
    transform: scale(1.1);
  }
}
